@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

.container {
    width: 75%;
    height: 50%;
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
}

.username-input {
    font-family: "Audiowide";
}

.loginDiv {
    position: relative;
    display: inline-block;
    text-align: center;
    width: 40%;
    height: 100vh;
    border-radius: 5vh;
    min-width: 300px;
}

.login-container {
    display: inline-block;
    width: 100%;

}

.logo-container {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: auto;

}

.login-body {
    background-color: #222a37;
    border: .25vh solid #1f2632;
    border-radius: 5vh;
    padding: 2vh;
}

.logoDiv {
    position: relative;
    margin: 5%;
    width: 50%;
    height: 50%;
    border: .25vh solid #1f2632;
    border-radius: 50vh;
}

.logoDiv:hover {
    background-color: rgb( 9, 132, 227, .75);
    animation: content_pulse 2s infinite;
    cursor: pointer;
}

.logoDiv .logo {
    width: 100%;
}


.text-input {
    display: inline-block;
    margin-top: 1vh;
    width: auto;
    text-align: center;
    width: 100%;
}

.text-input .text-label{
    display: inline-block;
    margin: auto;
    width: 95%;
    padding-left: 2vh;
    background-color: #646a73;
    text-align: left;
    font-size: 2vh;
    color: white

}

.text-input input {
    align-self: center;
    font-size: 2vh;
    width: 90%;
    padding: 1vh;
}


.login-buttons-div .login-button {
    margin: 1vh;
} 

.login-buttons-div a{
    color: #7a7f87;
    text-decoration: underline;
    font-size: 2vh;
}

.login-buttons-div a:hover {
    color: #0984e3;
    cursor: pointer;
}

.messageBox {
    color: white;
}


.alert-div {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 1;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}